nav img {
  height: 24px;
  opacity: 0.9;
}

svg {
  border: 1px solid black;
}

#home {
  background: linear-gradient(rgba(146, 146, 146, 0.4), rgba(146, 146, 146, 0.4)), url('./img/seattle-skyline-vanilla.jpg');
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  height: 400px;
}

#gallery div, #gallery div img {
  width: 300px;
}

#gallery img {
  box-sizing: border-box;
  border: 4px solid gray;
}

#map path {
  fill: gray;
}

#legend circle {
  fill: none;
  stroke: brown;
  stroke-width: 2;
}

#legend text {
  font-size: 12px;
}

#legend text,
.label {
  font-family: Arial, Helvetica, sans-serif;
  text-anchor: middle;
}

#quake-locations circle {
  fill: orange;
  stroke: brown;
}

.front-article {
  margin: 12em 0;
}

.clear-spacing {
  margin: 0;
}

.project-card {
  width: 14rem;
}

/* Card hovering functionality */

.hidden {
  display: none;
}

.report-article {
  margin: 2em 0;
  overflow: visible;
}

.label {
  font-size: 14px;
}

.shoutout {
  padding-left: 30px;
}

.hidden {
  display: none;
}
